                         "pic4 pic4 pic4 pic4 pic4 pic4 pic4 pic4 pic5 pic5 pic5 pic5 pic5 pic5 pic5 pic5 pic6 pic6 pic6 pic6 pic6 pic6 pic6 pic6 pic6 pic6 pic6 pic6";
  }
}

.advice__item {
