.advice__item-img:after {
  content: "";
  opacity: 1;
  background: linear-gradient(rgba(19, 19, 19, 0) 28.65%, rgba(19, 19, 19, .52) 72.4%, #131313 100%);
  transition: opacity 1s;
  position: absolute;
  top: 0;
  left: 0;
}

.advice__item-img:after, .advice__item-img {
  width: 100%;
  height: 100%;
}

.advice__item:hover .advice__item-img:after {
  opacity: .7;
}

.advice__item-img {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: transform 1s;
}

.advice__item:hover .advice__item-img {
  transform: scale(1.1);
}

.advice__item--grid1 .advice__item-img {
  background-image: url("bg-img-1.250c0569.jpg");
}

.advice__item--grid2 .advice__item-img {
